<script>
const textEncoder = new TextEncoder()

const defaultDeviceInfo = {
  application: '',
  firmware: '',
  device_name: '',
  device_version: '',
}

export default {
  data: () => ({
    terminalWriter: null,
    terminalEvents: [], // events that are cleared by clear button
  }),

  methods: {
    terminalOnMessage(msg) {
      return true
    },

    terminalClear() {
      this.$store.commit('terminalMessagesClear')
      this.$root.$emit('messageAppended', {})

      this.terminalEvents = []
    },

    async terminalWrite(msg) {
      if (!this.terminalWriter || !msg) {
        return
      }

      //console.log('UART input: ' + msg)
      const outputBuffer = textEncoder.encode(msg)

      await this.terminalWriter.write(outputBuffer)

      await new Promise((resolve) => setTimeout(resolve, 50))
    },

    async terminalConnectionStart(deviceInfoCallback) {
      if (this.terminalReader) {
        this.$log.warn('Connection has already been established.')
        deviceInfoCallback(null)
        return
      }

      let port = null
      try {
        const filters = [
          {
            // KitProg3
            usbVendorId: 0x04b4, // (Cypress Semiconductor)
            usbProductId: 0xf155,
          },
          {
            // SOM
            usbVendorId: 0x04b4, // (Cypress Semiconductor)
            usbProductId: 0x0003,
          },
          {
            // DAP
            usbVendorId: 0x0d28, // (ARM Ltd)
            usbProductId: 0x0204,
          },
        ]
        port = await navigator.serial.requestPort({ filters: filters })
      } catch (error) {
        console.info('User did not select a serial device.', error)
        deviceInfoCallback(null)
        return
      }

      try {
        await port.open({ baudRate: 115200 })

        const textDecoder = new TextDecoderStream()
        const readableStreamClosed = port.readable.pipeTo(textDecoder.writable)
        const reader = textDecoder.readable.getReader()

        this.terminalWriter = port.writable.getWriter()

        this.terminalClear()

        this.$store.commit('terminalDeviceInfoSet', defaultDeviceInfo)
        this.$root.$emit('messageAppended', {})

        // at that point, we are connected and the solution is ready to go
        this.$store.commit('terminalSetDevice', reader)

        this.$log.info('Connected to device. Query device information...')

        const enableVerboseCommand = `\x0Dverbose enable\x0D\x1B`
        await this.terminalWrite(enableVerboseCommand)

        const that = this
        this.terminalQueryAttributes('device_info', '[DEVICE_INFO]')
          .then((deviceInfo) => {
            const terminalDeviceInfo = { ...defaultDeviceInfo, ...deviceInfo }

            that.$store.commit('terminalDeviceInfoSet', terminalDeviceInfo)
            that.$root.$emit('messageAppended', {})

            deviceInfoCallback(terminalDeviceInfo)
          })
          .catch((reason) => {
            console.warn(
              'Failed to retrieve device_info information, error = ' + reason
            )
            deviceInfoCallback(defaultDeviceInfo)
          })

        let receivedMessage = ''
        try {
          while (true) {
            const { value, done } = await reader.read()
            if (done) {
              // allow the serial port to be closed later
              reader.releaseLock()
              break
            }
            if (value) {
              receivedMessage += value
              let receivedMessageList = receivedMessage.split('\r\n')

              if (receivedMessageList.length > 1) {
                receivedMessage = receivedMessageList.pop()

                const that = this
                receivedMessageList = receivedMessageList.filter((msg) =>
                  that.terminalOnMessage(msg)
                )

                for (const msg of receivedMessageList) {
                  console.log('Terminal', msg)
                }

                // do not execeed max events
                const terminalMessagesMaxNumber =
                  this.$store.getters.terminalMessagesMaxNumber
                if (this.terminalEvents.length > terminalMessagesMaxNumber) {
                  const itemsToBeRemoved =
                    this.terminalEvents.length - terminalMessagesMaxNumber
                  this.terminalEvents =
                    this.terminalEvents.slice(itemsToBeRemoved)
                }

                this.$store.commit('terminalMessagesAdd', receivedMessageList)
                this.$root.$emit('messageAppended', {})
              }
            }
          }
        } catch (error) {
          this.$log.warn(
            'Failed to access serial port. It may got disconnected. Error details = ' +
              error
          )
        }

        this.terminalWriter.releaseLock()
        this.terminalWriter = null

        await readableStreamClosed.catch(() => {
          // ignore the error
        })

        await port.close()
      } catch (error) {
        this.$log.warn(
          'Failed to open serial port. It may already be opened. Error details = ' +
            error
        )
        deviceInfoCallback(null)
      }
    },

    async terminalConnectionStop() {
      this.$store.commit('terminalSetDevice', null)

      const terminalDeviceInfo = { ...defaultDeviceInfo }

      this.$store.commit('terminalDeviceInfoSet', terminalDeviceInfo)
    },

    terminalParseLine(line, tag) {
      if (line.startsWith(tag)) {
        const keyIndex = line.indexOf(' ')
        const valueIndex = line.indexOf(' ', keyIndex + 1)
        if (keyIndex >= 0 && valueIndex >= 0) {
          const key = line.substring(keyIndex + 1, valueIndex)
          let value = line.substring(valueIndex + 1)

          const isInteger = /^\d+$/
          const isFloatingPoint = /^[+-]?\d+(\.\d+)?$/

          // convert to target format
          if (value === 'enable') {
            value = true
          } else if (value === 'disable') {
            value = false
          } else if (isInteger.test(value)) {
            value = parseInt(value)
          } else if (isFloatingPoint.test(value)) {
            value = parseFloat(value)
          }

          return {
            key: key,
            value: value,
          }
        }
      }

      return null
    },

    async terminalQueryAttributes(command, tag, timeout = 1500) {
      // this method gets the device info
      const queryCommand = `\x0D${command}\x0D\x1B`
      await this.terminalWrite(queryCommand)

      // we wait some time for the response
      await this.$sleep(timeout)

      const terminalMessages = this.terminalMessages

      const indexEnd = terminalMessages.lastIndexOf(tag)
      let indexBegin = -1
      if (indexEnd > 0) {
        indexBegin = terminalMessages.lastIndexOf(tag, indexEnd - 1)
      }
      if (indexBegin < 0 || indexEnd < 0) {
        this.$log.warn(
          `Failed to query for command "${command}" with tag "${tag}".`
        )
        return {}
      }

      let attributes = {}
      for (let i = indexBegin; i < indexEnd; i++) {
        const line = terminalMessages[i]

        const deviceAttribute = this.terminalParseLine(line, tag)

        if (deviceAttribute) {
          attributes[deviceAttribute.key] = deviceAttribute.value
        }
      }

      return attributes
    },
  },

  computed: {
    terminalLastMessage() {
      const terminalMessages = this.terminalMessages

      if (terminalMessages.length) {
        return terminalMessages[terminalMessages.length - 1]
      } else {
        return ''
      }
    },

    terminalReader() {
      return this.$store.getters.terminalDevice
    },

    terminalDeviceInfo() {
      return this.$store.getters.terminalDeviceInfo
    },

    terminalMessages() {
      return this.$store.getters.terminalMessages
    },
  },
}
</script>
