import Vue from 'vue'
import VueRouter from 'vue-router'
import store from './VuexStore'

import Dashboard from '../views/Dashboard.vue'
import DesktopDevelopment from '../views/DesktopDevelopment.vue'
import MotionAngle from '../views/MotionAngle.vue'
import Presence from '../views/Presence.vue'
import License from '../views/License.vue'

let routes = [
  {
    path: '/',
    name: 'Dashboard',
    component: Dashboard,
  },
  {
    path: '/Presence',
    name: 'Presence',
    component: Presence,
  },
  {
    path: '/MotionAngle',
    name: 'MotionAngle',
    component: MotionAngle,
  },
  {
    path: '/Desktop',
    name: 'Desktop',
    component: DesktopDevelopment,
  },
  {
    path: '/License',
    name: 'License',
    component: License,
    props: true,
  },
  { path: '*', redirect: '/' },
]

Vue.use(VueRouter)
const router = new VueRouter({
  mode: 'hash',
  routes: routes,
})
router.beforeEach((to, from, next) => {
  const licenseAgreement = store.getters.licenseAgreement
  if (!licenseAgreement && to.name !== 'License') {
    next({
      name: 'License',
      params: {
        goto: to.name,
      },
    })
    return
  }

  store.commit('terminalSetDevice', null)
  router.app.$log.info('')

  next()
})

export default router
