<template>
  <v-app>
    <v-app-bar app clipped-left>
      <v-img
        max-width="70"
        contain
        :src="$res('img/icons/logo.png')"
        style="cursor: pointer"
        @click="$router.push({ path: 'dashboard' })"
      >
      </v-img>

      <v-spacer></v-spacer>
      <v-toolbar-title class="pl-4">
        Infineon - XENSIV™ config tool
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>

    <v-main>
      <v-container fluid style="height: calc(100vh - 56px); overflow-y: auto">
        <v-row style="height: calc(100% - 56px)">
          <iframe
            :src="$res('/manuals/License.pdf#toolbar=0')"
            width="100%"
            height="100%"
          >
          </iframe>
        </v-row>

        <v-row>
          <v-col></v-col>
          <v-col>
            <v-card elevation="0">
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="primary"
                  @click="agreeLicense()"
                  id="cyAgreeLicense"
                >
                  Accept
                </v-btn>
                <v-spacer></v-spacer>
              </v-card-actions> </v-card
          ></v-col>
          <v-col></v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: 'License',

  components: {},

  mixins: [],

  props: {
    goto: {
      type: String,
    },
  },

  data: () => ({}),

  computed: {},

  methods: {
    agreeLicense() {
      this.$store.commit('licenseAgreement', true)
      const goto = this.goto ? this.goto : 'Dashboard'
      this.$router.push({ name: goto })
    },
  },

  mounted() {},
}
</script>

<style></style>
