<template>
  <v-dialog
    class="mx-auto"
    max-width="600"
    persistent
    :value="value"
    @input="$emit('input', $event)"
  >
    <v-card>
      <v-card-title class="headline">
        {{ title }}
      </v-card-title>
      <v-card-text>
        {{ description }}

        <slot name="description"></slot>
      </v-card-text>

      <v-card-actions v-if="apply2Text && $vuetify.breakpoint.xsOnly">
        <v-spacer />
        <v-btn depressed :color="apply2Color" @click="apply2()">
          {{ apply2Text }}
        </v-btn>
      </v-card-actions>

      <v-card-actions>
        <v-btn
          id="cyDialogBtnApply2"
          v-if="apply2Text && $vuetify.breakpoint.smAndUp"
          depressed
          :color="apply2Color"
          @click="apply2()"
        >
          {{ apply2Text }}
        </v-btn>

        <v-spacer />

        <v-btn
          id="cyDialogBtnCancel"
          v-if="cancelEnabled"
          depressed
          :color="cancelColor"
          @click="cancel()"
        >
          {{ cancelText }}
        </v-btn>
        <v-btn
          id="cyDialogBtnApply"
          v-if="applyEnabled"
          depressed
          :color="applyColor"
          @click="apply()"
        >
          {{ applyText }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'AcknowledgeDialog',

  mixins: [],

  props: {
    title: {
      type: String,
      required: true,
    },

    description: {
      type: String,
      default: '',
    },

    cancelEnabled: {
      type: Boolean,
      default: true,
    },

    cancelText: {
      type: String,
      default: 'Cancel',
    },

    cancelColor: {
      type: String,
      default: '', // error, primary
    },

    applyEnabled: {
      type: Boolean,
      default: true,
    },

    applyText: {
      type: String,
      default: 'Yes',
    },

    applyColor: {
      type: String,
      default: 'primary',
    },

    apply2Text: {
      type: String,
      default: '',
    },

    apply2Color: {
      type: String,
      default: 'error',
    },

    value: {
      type: Boolean,
      default: false,
    },
  },

  data: () => ({}),

  computed: {},

  mounted() {},

  methods: {
    close() {
      this.$emit('input', false)
      this.$emit('close')
    },

    cancel() {
      this.$emit('cancel')
      this.close()
    },

    apply() {
      this.$emit('apply')
      this.close()
    },

    apply2() {
      this.$emit('apply2')
      this.close()
    },
  },
}
</script>

<style></style>
