<template>
  <v-app>
    <v-app-bar app clipped-left>
      <v-tooltip right>
        <template v-slot:activator="{ on, attrs }">
          <v-img
            max-width="70"
            contain
            :src="$res('img/icons/logo.png')"
            style="cursor: pointer"
            @click="goToPreviousOrDashboard"
            v-bind="attrs"
            v-on="on"
          >
          </v-img>
        </template>
        <span>Go back to dashboard.</span>
      </v-tooltip>

      <v-tooltip right>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            id="cyBtnConnect"
            :disabled="flashProgress >= 0"
            :color="terminalReader ? 'info' : undefined"
            tile
            class="ml-4"
            @click="toggleConnection()"
            v-bind="attrs"
            v-on="on"
            ><v-icon left> mdi-power </v-icon
            >{{
              terminalReader ? 'Disconnect from board' : 'Connect to board'
            }}</v-btn
          >
        </template>
        <span>Connect or disconnect to the device.</span>
      </v-tooltip>

      <v-tooltip v-if="applicationFirmware" right>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            id="cyBtnFlash"
            :disabled="!!terminalReader"
            tile
            class="ml-12"
            @click="writeApplicationFirmware"
            v-bind="attrs"
            v-on="on"
            ><v-icon left> mdi-flash </v-icon>Flash to device</v-btn
          >
        </template>
        <span>Flash this application to the device.</span>
      </v-tooltip>

      <v-btn
        icon
        :color="navBar.dapLinkManual ? 'info' : undefined"
        @click="
          navBar.help = false
          navBar.dapLinkManual = !navBar.dapLinkManual
          reloadManual()
        "
      >
        <v-icon>mdi-help</v-icon>
      </v-btn>

      <v-spacer />

      <v-tooltip v-if="onExportCSV" left>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            id="cyBtnClear"
            tile
            class="ml-2"
            @click="dialog.askClearRecordings = true"
            v-bind="attrs"
            v-on="on"
            >Clear</v-btn
          >
        </template>
        <span>Clear recordings</span>
      </v-tooltip>

      <v-tooltip v-if="onExportCSV" left>
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            id="cyBtnSaveRecordings"
            tile
            class="ml-4"
            @click="saveRecordings()"
            v-bind="attrs"
            v-on="on"
            >Save Recordings</v-btn
          >
        </template>
        <span>Save recordings as an Excel-CSV-file.</span>
      </v-tooltip>
    </v-app-bar>

    <v-navigation-drawer app clipped mini-variant permanent>
      <v-list>
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnInfo"
              :color="navBar.info ? 'info' : undefined"
              x-large
              icon
              @click="navBar.info = !navBar.info"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-information-outline</v-icon>
            </v-btn>
          </template>
          <span>Application Information</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnDevice"
              :color="navBar.device ? 'info' : undefined"
              x-large
              icon
              @click="navBar.device = !navBar.device"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-memory</v-icon>
            </v-btn>
          </template>
          <span>Device Information</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnSettings"
              :color="navBar.settings ? 'info' : undefined"
              x-large
              icon
              @click="navBar.settings = !navBar.settings"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-tune-vertical</v-icon>
            </v-btn>
          </template>
          <span>Application Settings</span>
        </v-tooltip>

        <v-tooltip v-if="applicationManual" right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnHelp"
              :color="navBar.help ? 'info' : undefined"
              x-large
              icon
              @click="
                navBar.dapLinkManual = false
                navBar.help = !navBar.help
                reloadManual()
              "
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-help</v-icon>
            </v-btn>
          </template>
          <span>Help</span>
        </v-tooltip>

        <slot name="plotNavigationBar"></slot>

        <v-divider></v-divider>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnTerminal"
              :color="navBar.term ? 'info' : undefined"
              x-large
              icon
              @click="toggleTerminal()"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-console</v-icon>
            </v-btn>
          </template>
          <span>Terminal</span>
        </v-tooltip>
      </v-list>

      <template v-slot:append>
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnSaveConfig"
              x-large
              icon
              @click="saveConfiguration()"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-content-save-outline</v-icon>
            </v-btn>
          </template>
          <span>Save Configuration</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnLoadConfig"
              x-large
              icon
              @click="$refs.fileUpload.click()"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-file-upload-outline</v-icon>
            </v-btn>
            <input
              v-show="false"
              id="txtFileUpload"
              type="file"
              ref="fileUpload"
              @change="loadConfiguration"
              accept=".json"
            />
          </template>
          <span>Load Configuration</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnResetConfig"
              x-large
              icon
              @click="dialog.askResetConfiguration = true"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-refresh</v-icon>
            </v-btn>
          </template>
          <span>Reset Configuration</span>
        </v-tooltip>
      </template>
    </v-navigation-drawer>

    <v-main>
      <v-container
        v-if="navBar.help || navBar.dapLinkManual"
        fluid
        :style="
          navBar.term
            ? 'height: calc(100vh - 56px - 216px); overflow-y: auto'
            : 'height: calc(100vh - 56px);'
        "
      >
        <v-row>
          <v-spacer></v-spacer>
          <v-col cols="12">
            <h2 class="font-weight-light" style="text-align: center">
              {{ applicationName }}
              <span class="text-subtitle-1"
                >Infineon - XENSIV™ config tool
              </span>
            </h2>
          </v-col>
          <v-spacer></v-spacer>
        </v-row>

        <v-row style="height: calc(100% - 44px)" id="cyHelp">
          <iframe :src="manualLink" width="100%" height="100%"> </iframe>
        </v-row>
      </v-container>
      <v-container
        v-else
        fluid
        :style="
          navBar.term
            ? 'height: calc(100vh - 56px - 216px); overflow-y: auto'
            : 'height: calc(100vh - 56px); overflow-y: auto'
        "
      >
        <v-row>
          <v-spacer></v-spacer>
          <v-col cols="12">
            <h2 class="font-weight-light" style="text-align: center">
              {{ applicationName }}
              <span class="text-subtitle-1"
                >Infineon - XENSIV™ config tool
              </span>
            </h2>
          </v-col>
          <v-spacer></v-spacer>
        </v-row>
        <v-row style="height: 100%">
          <v-col
            v-if="navBar.info || navBar.device || navBar.settings"
            cols="3"
          >
            <v-container class="pa-0">
              <v-card
                v-if="navBar.info"
                id="cyInfo"
                :width="dialog.panelWidth"
                :max-width="dialog.panelWidth"
                outlined
                class="mb-4"
                ><v-card-title> Application Information </v-card-title>
                <v-card-text>
                  <slot name="applicationInformation"></slot>
                </v-card-text>
              </v-card>
              <v-card
                v-if="navBar.device"
                id="cyDeviceInfo"
                :disabled="!terminalReader"
                :width="dialog.panelWidth"
                :max-width="dialog.panelWidth"
                outlined
                class="mb-4"
                ><v-card-title> Device Information </v-card-title>
                <v-card-text>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        id="cyTextApplication"
                        label="Application"
                        v-model="terminalDeviceInfo.application"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <span>Application name</span>
                  </v-tooltip>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        id="cyTextFirmware"
                        label="Firmware"
                        v-model="terminalDeviceInfo.firmware"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <span>Firmware version</span>
                  </v-tooltip>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        id="cyTextDevice"
                        label="Device"
                        v-model="terminalDeviceInfo.device_name"
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <span>Device name</span>
                  </v-tooltip>
                  <v-tooltip right>
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        id="cyTextHardware"
                        label="Hardware revision"
                        v-model="terminalDeviceInfo.device_version"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <span>Hardware revision</span>
                  </v-tooltip>
                </v-card-text>
              </v-card>
              <v-card
                v-if="navBar.settings"
                id="cySettings"
                :width="dialog.panelWidth"
                :max-width="dialog.panelWidth"
                outlined
                class="mb-4"
                ><v-card-title> Application Settings </v-card-title>
                <v-card-text>
                  <slot name="applicationSettings"></slot>
                </v-card-text>
                <v-card-actions>
                  <v-btn
                    v-if="!!onExportModusToolboxConfigurationHeader"
                    @click="exportModusToolboxConfigurationHeader()"
                    id="cyExportConfiguration"
                  >
                    Export
                  </v-btn>
                  <v-spacer></v-spacer>
                  <v-btn
                    :disabled="!terminalReader"
                    @click="applyConfiguration()"
                    id="cyApplyConfiguration"
                  >
                    Apply
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-container>
          </v-col>
          <v-col>
            <v-container fluid style="height: 100%">
              <slot name="plotView"></slot>
            </v-container>
          </v-col>
        </v-row>
      </v-container>
    </v-main>

    <v-footer bottom fixed style="margin-left: 56px" class="pa-0">
      <v-container fluid class="ma-0 pa-0">
        <v-virtual-scroll
          id="cyTerminal"
          v-if="navBar.term"
          class="ma-2"
          :items="terminalMessages"
          height="200"
          item-height="20"
        >
          <template v-slot:default="{ item }">
            <div class="terminal-row" v-bind:title="item">
              {{ item }}
            </div>
          </template>
        </v-virtual-scroll>

        <v-system-bar id="cyStatusBar" :color="systemBarMessage.color">
          <span>{{ systemBarMessage.message }}</span>
        </v-system-bar>
      </v-container>
    </v-footer>

    <AcknowledgeDialog
      title="No devices found"
      description="Please note that flashing an appliation to a device is only supported in DAPLink mode. Please refer to the user manual, how to open switch between CMSIS-DAP bulk and CMSIS-DAP link mode."
      v-model="dialog.noDAPDevicesFound"
      applyText="Open Manual"
      @apply="navBar.dapLinkManual = true"
    />

    <AcknowledgeDialog
      title="Clear recordings"
      description="This action will clear all recordings. Are you sure that you want to proceed?"
      v-model="dialog.askClearRecordings"
      @apply="terminalClear()"
    />

    <AcknowledgeDialog
      title="Reset configuration"
      description="Do you want to reset to default configuration?"
      v-model="dialog.askResetConfiguration"
      @apply="resetConfiguration()"
    />

    <AcknowledgeDialog
      title="Matching application not found"
      v-model="dialog.unknownApplication"
      :cancelEnabled="false"
      :applyText="'Okay'"
      @apply="toggleConnection()"
    >
      <template v-slot:description>
        It seems that the connected device does not run the
        {{ applicationName }} application. Please use the flash button to update
        the firmware on the device. Note, this will erease all exsiting data on
        the device.
      </template>
    </AcknowledgeDialog>

    <v-overlay :value="pendingOperation">
      <v-progress-circular
        v-if="flashProgress < 0"
        indeterminate
        size="80"
        color="#AB377A"
      >
      </v-progress-circular>

      <v-progress-circular
        v-else
        rotate="-90"
        size="80"
        :value="flashProgress"
        color="#AB377A"
      >
        {{ Math.floor(flashProgress) }}
      </v-progress-circular>
    </v-overlay>
  </v-app>
</template>

<script>
import AcknowledgeDialog from '../components/AcknowledgeDialog'

import Configuration from '../mixins/Configuration'
import Download from '../mixins/Download'
import Terminal from '../mixins/Terminal'
import Flash from '../mixins/Flash'

export default {
  name: 'SolutionView',

  components: { AcknowledgeDialog },

  mixins: [Configuration, Download, Terminal, Flash],

  props: {
    applicationName: {
      type: String, // 'Motion angle'
      required: true,
    },

    applicationId: {
      type: String, // 'PSoC™ 6 MCU : Motion angle', must match the firmware name
      required: true,
    },

    applicationShortName: {
      type: String, // 'MotionAngle', short name of the full application name used for settings files
      required: true,
    },

    applicationConfiguration: {
      type: Object,
      required: true,
    },

    applicationDefaultConfiguration: {
      type: Object,
      required: true,
    },

    applicationManual: {
      type: String, // path to pdf manual
      default: '',
    },

    applicationFirmware: {
      type: String, // path to firmware binary
      default: '',
    },

    onNewMessage: {
      type: Function,
      default: null,
    },

    onExportCSV: {
      type: Function,
      default: null,
    },

    onExportModusToolboxConfigurationHeader: {
      type: Function,
      default: null,
    },

    onDeviceConnection: {
      type: Function,
      default: null,
    },
  },

  data: () => ({
    dialog: {
      panelWidth: 350, // left panel width
      askClearRecordings: false,
      askResetConfiguration: false,
      noDAPDevicesFound: false,
      unknownApplication: false,
    },

    navBar: {
      info: true,
      device: false,
      settings: true,
      help: false,
      term: false,
      dapLinkManual: false,
    },

    refreshHelpView: false,
    pendingOperation: false,
    scrollContainer: null,
  }),

  computed: {
    systemBarMessage() {
      const msg = this.$store.getters.infoMessageReceived
      if (msg) {
        return msg
      } else {
        return {
          message: '',
          level: 'info',
          color: undefined,
        }
      }
    },

    manualLink() {
      if (this.refreshHelpView) return ''

      return this.navBar.dapLinkManual
        ? this.$res('manuals/CSK_Manual.pdf#page=46')
        : this.applicationManual
    },
  },

  methods: {
    reloadManual() {
      if (!this.navBar.help && !this.navBar.dapLinkManual) {
        return
      }

      const that = this

      this.refreshHelpView = true

      setTimeout(() => {
        that.refreshHelpView = false
      }, 100)
    },

    goToPreviousOrDashboard() {
      this.$router.push({ path: 'Dashboard' })
    },

    terminalOnMessage(msg) {
      if (this.onNewMessage) {
        const newEvent = this.onNewMessage(msg)
        if (newEvent || newEvent === null) {
          if (newEvent) {
            this.terminalEvents.push(newEvent)
          }

          return false
        }
      }

      return true
    },

    _verifyConfiguration(solutionConfig) {
      const applicationConfiguration = {
        ...this.applicationConfiguration,
      }
      for (const key in this.applicationConfiguration) {
        if (key in solutionConfig) {
          applicationConfiguration[key] = solutionConfig[key]
        }
      }
      return applicationConfiguration
    },

    flashNoDAPLinkDevicesFound() {
      this.dialog.noDAPDevicesFound = true
    },

    async toggleConnection() {
      if (this.terminalReader) {
        await this.terminalConnectionStop()

        if (this.onDeviceConnection) {
          this.onDeviceConnection(false, {
            ...this.applicationDefaultConfiguration,
          })
        }

        this.$log.info('Device disconnected.')
      } else {
        this.pendingOperation = true

        const that = this
        this.terminalConnectionStart(async (deviceInfo) => {
          if (!deviceInfo) {
            that.pendingOperation = false
            return
          }

          that.$log.info(
            'Device information received. Query solution configuration...',
            deviceInfo
          )

          if (deviceInfo.application === this.applicationId) {
            try {
              const solutionConfig = await this.terminalQueryAttributes(
                'solution_config',
                '[CONFIG]'
              )

              that.$log.info('Device connection established.')

              if (this.onDeviceConnection) {
                const applicationConfiguration =
                  this._verifyConfiguration(solutionConfig)

                this.onDeviceConnection(true, applicationConfiguration)
              }
            } catch (error) {
              this.$log.warn(
                'Could not load solution configuration, got error = ' + error
              )
            }
          } else {
            console.log(
              'Unknown application found, exspected = ' +
                this.applicationId +
                ', got = ',
              deviceInfo
            )
            this.dialog.unknownApplication = true
          }

          that.pendingOperation = false
        })
      }
    },

    async saveRecordings() {
      if (!this.onExportCSV) {
        console.warn('onExportCSV has not been set.')
        return
      }

      this.pendingOperation = true

      try {
        let csvFile = this.onExportCSV(this.terminalEvents)

        const csvFileAsString = csvFile.join('\r\n')
        csvFile = null // free memory
        const blob = new Blob([csvFileAsString], { type: 'text/csv' })

        const now = new Date()
        const datetime =
          now.getUTCFullYear().toString() +
          '-' +
          (now.getUTCMonth() + 1).toString() +
          '-' +
          now.getUTCDate() +
          '_' +
          now.getUTCHours() +
          '-' +
          now.getUTCMinutes() +
          '-' +
          now.getUTCSeconds()
        const applicationShortName = this.applicationShortName
        const filename = `${applicationShortName}_Recordings_${datetime}.csv`

        await this.downloadBlob(blob, filename)
      } catch (error) {
        this.$log.warn('Failed to save recordings, got error = ' + error)
      }

      this.pendingOperation = false
    },

    async exportModusToolboxConfigurationHeader() {
      try {
        const header = this.onExportModusToolboxConfigurationHeader()
        const blob = new Blob([header], { type: 'text/plain' })

        const applicationShortName = this.applicationShortName
        const filename = `${applicationShortName}_Settings.h`
        return await this.downloadBlob(blob, filename)
      } catch (error) {
        this.$log.warn(
          'Failed to export configuration header, got error = ' + error
        )
      }
    },

    async applyConfiguration() {
      this.pendingOperation = true

      try {
        const newApplicationConfig = { ...this.applicationConfiguration }

        const solutionConfig = await this.configSetDictionary(
          newApplicationConfig
        )

        const effectiveConfiguration = this._verifyConfiguration(solutionConfig)
        this.onDeviceConnection(true, effectiveConfiguration)

        const msg = 'Configuration applied and effective values read back'
        this.$log.popup(msg)
      } catch (error) {
        this.$log.warn('Failed to apply configuration, got error = ' + error)
      }

      this.pendingOperation = false
    },

    async resetConfiguration() {
      this.pendingOperation = true

      try {
        const newApplicationConfig = { ...this.applicationDefaultConfiguration }

        const solutionConfig = await this.configSetDictionary(
          newApplicationConfig
        )

        const effectiveConfiguration = this._verifyConfiguration(solutionConfig)
        this.onDeviceConnection(true, effectiveConfiguration)

        const msg = 'Reset configuration and effective values read back'
        this.$log.popup(msg)
      } catch (error) {
        this.$log.warn('Failed to reset configuration, got error = ' + error)
      }

      this.pendingOperation = false
    },

    async saveConfiguration() {
      const applicationShortName = this.applicationShortName
      return await this.configSave(`${applicationShortName}_Settings.json`)
    },

    async loadConfiguration(ev) {
      const file = ev.target.files[0]
      this.$refs.fileUpload.value = ''

      this.pendingOperation = true

      try {
        const loadedConfig = await this.configLoad(file)

        if (loadedConfig) {
          const effectiveConfiguration = this._verifyConfiguration(
            loadedConfig.config
          )
          this.onDeviceConnection(true, effectiveConfiguration)

          const msg = 'Loaded configuration, values not yet applied'
          this.$log.popup(msg)
        }
      } catch (error) {
        this.$log.warn('Failed to load configuration, got error = ' + error)
      }

      this.pendingOperation = false
    },

    async toggleTerminal() {
      this.navBar.term = !this.navBar.term

      setTimeout(() => {
        this.scrollContainer = this.navBar.term
          ? document.getElementById('cyTerminal')
          : null
      }, 200)
    },

    async writeApplicationFirmware() {
      try {
        this.pendingOperation = true

        await this.flashFirmware(this.applicationFirmware)
      } catch (error) {
        this.$log.warn('Failed to flash application, got error = ' + error)
      }

      this.pendingOperation = false
    },
  },

  mounted() {
    this.$root.$on('messageAppended', (data) => {
      if (
        this.scrollContainer &&
        this.scrollContainer.scrollHeight > 200 &&
        this.scrollContainer.scrollHeight - this.scrollContainer.scrollTop <=
          280
      ) {
        this.scrollContainer.scrollTop = this.scrollContainer.scrollHeight
      }
    })
  },
}
</script>

<style></style>
