<template>
  <v-card class="ma-1" max-width="640" elevation="0" outlined>
    <v-container>
      <v-row>
        <v-col>
          <v-card-title>{{ title }}</v-card-title>
          <v-card-text>
            <p>
              {{ description }}
            </p>
          </v-card-text></v-col
        >
        <v-avatar class="ma-4" size="125" tile>
          <v-img
            :src="picture ? picture : $res('img/icons/logo_square.png')"
          ></v-img>
        </v-avatar>
      </v-row>
    </v-container>

    <v-card-actions class="pt-8">
      <v-btn absolute bottom right @click="goto" :disabled="!route">
        Open
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: 'SolutionCard',

  props: {
    title: {
      type: String,
      required: true,
    },

    description: {
      type: String,
      required: true,
    },

    picture: {
      type: String,
      default: '',
    },

    route: {
      type: String,
      default: '',
    },
  },

  methods: {
    goto() {
      if (this.route.startsWith('http')) {
        window.open(this.route, '_blank', 'noreferrer')
      } else {
        this.$router.push({ name: this.route })
      }
    },
  },
}
</script>

