<script>
const DAPjs = require('dapjs')

export default {
  data: () => ({
    flashProgress: -1,
  }),

  methods: {
    flashNoDAPLinkDevicesFound() {},

    async flashFirmware(firmwarePath) {
      let numDevices = 0

      const ifxVendorID = 0xd28

      let devices = await navigator.usb.getDevices()
      devices.forEach((device) => {
        console.log(
          `Product name: ${device.productName}, serial number ${device.serialNumber}`,
          device
        )

        if (device.vendorId == ifxVendorID) {
          numDevices = numDevices + 1
        }
      })

      if (!numDevices) {
        console.warn('No compatible devices found.')
        this.flashProgress = -1
        this.flashNoDAPLinkDevicesFound()
        return
      }

      let device = null
      try {
        device = await navigator.usb.requestDevice({
          filters: [{ vendorId: ifxVendorID }],
        })
      } catch (error) {
        console.info('User did not select a serial device.', error)
        this.flashProgress = -1
        return
      }

      try {
        const transport = new DAPjs.WebUSB(device)
        const target = new DAPjs.DAPLink(transport)

        const that = this

        // to high number of progress reports limits flashing
        let flashProgressCounter = 0
        target.on(DAPjs.DAPLink.EVENT_PROGRESS, (progress) => {
          if (flashProgressCounter % 50 == 0) {
            that.flashProgress = progress * 100
          }
          flashProgressCounter++
        })

        const buffer = await this.$http.get(firmwarePath, {
          responseType: 'arraybuffer', // Important
        })

        await target.connect()

        this.flashProgress = 0
        await target.flash(new Uint8Array(buffer.data))
        this.flashProgress = 100

        await target.disconnect()
      } catch (error) {
        this.$log.warn('Failed to flash application. Error details = ' + error)
      }

      this.flashProgress = -1
    },
  },
}
</script>
