import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const defaultDeviceInfo = {
  application: '',
  firmware: '',
  device_name: '',
  device_version: '',
}

let state = {
  state: {
    // last log message
    infoMessage: null,

    // maximum number of messages
    terminalMessagesMaxNumber: 2000,

    // holds all terminal messages
    terminalMessages: [],

    // holds the device information
    terminalDeviceInfo: defaultDeviceInfo,

    // holds the handle to the terminal device opened
    terminalDevice: null,

    // did the user accept the license
    licenseAgreement: false,
  },

  getters: {
    infoMessageReceived: (state) => {
      return state.infoMessage
    },

    terminalMessages: (state) => {
      return state.terminalMessages
    },

    terminalDeviceInfo: (state) => {
      return state.terminalDeviceInfo
    },

    terminalMessagesMaxNumber: (state) => {
      return state.terminalMessagesMaxNumber
    },

    terminalDevice: (state) => {
      return state.terminalDevice
    },

    licenseAgreement: (state) => {
      return state.licenseAgreement
    },
  },

  mutations: {
    licenseAgreement(state, check) {
      state.licenseAgreement = check ? true : false
    },

    onInformationMessage(state, message) {
      state.infoMessage = message
    },

    terminalSetDevice(state, device) {
      let terminalDevice = state.terminalDevice
      if (terminalDevice) {
        try {
          terminalDevice.cancel()
        } catch (error) {
          console.warn('Failed to close device connected, got error = ' + error)
        }
      }

      if (terminalDevice != device) {
        state.terminalDevice = device
      }
    },

    terminalMessagesClear(state) {
      state.terminalMessages = []
    },

    terminalMessagesAdd(state, messages) {
      let terminalMessages = state.terminalMessages
      terminalMessages.push(...messages)

      if (terminalMessages.length > state.terminalMessagesMaxNumber) {
        const itemsToBeRemoved =
          terminalMessages.length - state.terminalMessagesMaxNumber
        terminalMessages = terminalMessages.slice(itemsToBeRemoved)
      }

      state.terminalMessages = terminalMessages
    },

    terminalDeviceInfoSet(state, deviceInfo) {
      state.terminalDeviceInfo = { ...defaultDeviceInfo, ...deviceInfo }
    },
  },
}

const store = new Vuex.Store(state)

export default store
