<template>
  <v-app>
    <v-app-bar app clipped-left>
      <v-img max-width="70" contain :src="$res('img/icons/logo.png')"> </v-img>

      <v-spacer></v-spacer>
      <v-toolbar-title class="pl-4">
        Infineon - XENSIV™ config tool
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>

    <v-navigation-drawer app clipped mini-variant permanent>
      <v-list>
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnHelp"
              :color="showHelp ? 'info' : undefined"
              x-large
              icon
              @click="navBar.help = !navBar.help"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-help</v-icon>
            </v-btn>
          </template>
          <span>Help</span>
        </v-tooltip>

        <v-divider></v-divider>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnHelp"
              :color="navBar.gettingstarted ? 'info' : undefined"
              x-large
              icon
              @click="
                navBar.gettingstarted = !navBar.gettingstarted
                scrollTo('gettingstarted')
              "
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-arrow-top-right</v-icon>
            </v-btn>
          </template>
          <span>Getting started</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnHelp"
              :color="navBar.solution ? 'info' : undefined"
              x-large
              icon
              @click="
                navBar.solution = !navBar.solution
                scrollTo('solution')
              "
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-gamepad</v-icon>
            </v-btn>
          </template>
          <span>Solutions</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnHelp"
              :color="navBar.embedded ? 'info' : undefined"
              x-large
              icon
              @click="
                navBar.embedded = !navBar.embedded
                scrollTo('embedded')
              "
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-memory</v-icon>
            </v-btn>
          </template>
          <span>Embedded development</span>
        </v-tooltip>

        <v-tooltip right v-if="false">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnHelp"
              :color="navBar.desktop ? 'info' : undefined"
              x-large
              icon
              @click="
                navBar.desktop = !navBar.desktop
                scrollTo('desktop')
              "
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-monitor</v-icon>
            </v-btn>
          </template>
          <span>Desktop development</span>
        </v-tooltip>

        <v-tooltip right v-if="false">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnHelp"
              :color="navBar.system ? 'info' : undefined"
              x-large
              icon
              @click="
                navBar.system = !navBar.system
                scrollTo('system')
              "
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-land-plots</v-icon>
            </v-btn>
          </template>
          <span>System description</span>
        </v-tooltip>
      </v-list>
    </v-navigation-drawer>

    <v-main>
      <v-container
        v-if="showHelp"
        fluid
        style="height: calc(100vh - 56px); overflow-y: auto"
      >
        <v-row style="height: 100%" id="cyHelp">
          <iframe
            :src="$res('/manuals/CSK_Manual.pdf')"
            width="100%"
            height="100%"
          >
          </iframe>
        </v-row>
      </v-container>
      <v-container
        v-else
        fluid
        class="pa-8"
        style="height: calc(100vh - 56px); overflow-y: auto"
      >
        <div v-if="navBar.gettingstarted">
          <v-row>
            <p ref="gettingstarted" class="text-h4 pt-8">Getting started</p>
          </v-row>
          <v-row>
            <v-col>
              <v-card :max-width="maxWidthDescription" elevation="0">
                <v-card-text class="pa-0">
                  <p>
                    This Web tool supports Infineon`s &nbsp;<a
                      href="https://www.infineon.com/cms/de/applications/solutions/sensor-solutions/iot-sensors-platform/"
                      target="_blank"
                      ><strong>IoT Sensors Platform</strong></a
                    >: Rapid IoT prototyping powered by XENSIV™ sensors.<br /><br />

                    Infineon's XENSIV™ connected sensor kit (CSK) is the go-to
                    IoT sensors platform for IoT device development.<br />
                    It enables the creation of new prototype ideas based on
                    Infineon sensors including radar, environmental sensors and
                    others.<br />
                    The XENSIV™ connected sensor kit is a development kit for
                    customers in the IoT space looking to rapidly design,
                    develop and manufacture products based on a development
                    ecosystem. <br />
                    It supports testing sensor-driven IoT products and use cases
                    as well as prototyping. The kit enables customers to
                    significantly reduce the time from proof of concept to a
                    fully developed, finalized IoT design, for smart home
                    applications.
                  </p>

                  <p>
                    Get a feeling for the technologies and capabilities with our
                    Out-of-Box Example.
                    <span class="font-weight-bold">
                      First steps: "Sense, Compute, Connect"
                    </span>
                  </p>
                </v-card-text>
              </v-card>
            </v-col>
            <v-col cols="4">
              <v-img
                :src="$res('img/dashboard/csk_hw.png')"
                max-width="214"
                max-height="182"
              ></v-img>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <Card
                title="Quick IoT Experience"
                description="The Quick IoT Experience demonstrates how to read sensors and connect to the Rapid IoT connect cloud platform in less the 10 minutes."
                route="https://iot-cloud.infineon.com/#/login"
              ></Card>
            </v-col>
          </v-row>
          <v-row class="pt-8">
            <p class="pr-4 font-weight-bold">
              Choose your use-case and kick off your development!
            </p>
            <v-btn
              color="primary"
              @click="
                navBar.solution = true
                scrollTo('solution')
              "
            >
              <v-icon class="pr-2">mdi-gamepad</v-icon>Go to use-cases</v-btn
            >
          </v-row>
        </div>

        <div v-if="navBar.solution">
          <v-row>
            <p ref="solution" class="text-h4 pt-8">Use-cases</p>
          </v-row>
          <v-row>
            <v-col>
              <v-card :max-width="maxWidthDescription" elevation="0">
                <v-card-text class="pa-0">
                  <p>
                    This section offers <strong>ready-to-use</strong> solutions
                    that can directly be flashed to a XENSIV™ connected sensor
                    kit (CSK) using the webbrowser. The webbrowser will show the
                    results of the application while running the code examples
                    on Infineon´s
                    <a
                      href="https://www.infineon.com/cms/de/product/microcontroller/32-bit-psoc-arm-cortex-microcontroller/psoc-6-32-bit-arm-cortex-m4-mcu/"
                      >PSoC6™ Arm® Cortex® Microcontroller.</a
                    >
                  </p>
                </v-card-text>
              </v-card>
            </v-col>
            <v-col v-if="false" cols="4">
              <v-img
                :src="$res('img/dashboard/radar.png')"
                max-width="520"
                max-height="326"
              ></v-img>
            </v-col>
          </v-row>
          <v-row>
            <Card
              title="Presence"
              description="This solution provides extremely high accuracy in detecting both micro and macro motions. The ability to detect micro motion offers unique benefits over conventional technologies deployed to detect human presence, thus making it perfect for user interaction with devices."
              route="Presence"
              :picture="$res('img/usecases/presence.png')"
            ></Card>

            <!--
            <Card
              title="Motion and angle"
              description="This solution provides the closest moving object with a very low latency including distance, speed and angle information. It was designed and optimized to run with very little memory consumption."
              route="MotionAngle"
              :picture="$res('img/usecases/motion&angle.png')"
            ></Card>

            <Card
              title="Segmentation"
              description="This solution tracks two objects in front of the radar and keeps them alive even if they are not moving."
              route=""
              :picture="$res('img/usecases/segmentation.png')"
            ></Card>

            <Card
              title="Hand gesture recognition"
              description="This solution can recognize different hand gestures performed by the closest person in front of the radar."
              route=""
              :picture="$res('img/usecases/gesture.png')"
            ></Card>
            -->
          </v-row>

          <v-row class="pt-8">
            <p class="pr-4 font-weight-bold">
              Once you are done with our use-cases, let's see how we can build a
              fully embedded solution using ModusToolbox™.
            </p>
            <v-btn
              color="primary"
              @click="
                navBar.embedded = true
                scrollTo('embedded')
              "
            >
              <v-icon class="pr-2">mdi-memory</v-icon>Go to Embedded</v-btn
            >
          </v-row>
        </div>

        <div v-if="navBar.embedded" class="pt-12">
          <v-row>
            <p ref="embedded" class="text-h4 pt-8">Embedded development</p>
          </v-row>
          <v-row>
            <v-col>
              <v-card :max-width="maxWidthDescription" elevation="0">
                <v-card-text class="pa-0">
                  <p>
                    ModusToolbox™ Software is a modern, extensible development
                    environment supporting a wide range of Infineon
                    microcontroller devices, including&nbsp;<a
                      href="/cms/en/product/microcontroller/32-bit-psoc-arm-cortex-microcontroller/"
                      target="_blank"
                      ><strong>PSoC™ Arm® Cortex® Microcontrollers</strong></a
                    >,&nbsp;<a
                      href="/cms/en/product/microcontroller/32-bit-industrial-microcontroller-based-on-arm-cortex-m/"
                      target="_blank"
                      ><strong>XMC™ Industrial Microcontrollers</strong></a
                    >,&nbsp;<a
                      href="/cms/en/product/wireless-connectivity/"
                      target="_blank"
                      ><strong
                        >AIROC™ Wi-Fi devices, AIROC™ Bluetooth® devices</strong
                      ></a
                    >, and&nbsp;<a
                      href="/cms/en/product/universal-serial-bus-usb-power-delivery-controller/usb-c-and-power-delivery/ez-pd-pmg1-portfolio-high-voltage-mcus-usb-c-power-delivery/"
                      target="_blank"
                      ><strong>USB-C Power Delivery Microcontrollers</strong></a
                    >.
                  </p>
                  <p>
                    Provided as a collection of development tools, libraries,
                    and embedded runtime assets, ModusToolbox™ Software is
                    architected to provide a flexible and comprehensive
                    development experience
                  </p>
                  <p>
                    <strong>Run-Time Software</strong> comprised of middleware,
                    device drivers, and code examples is provided via an
                    extensive collection of GitHub-hosted repositories.&nbsp;
                    Explore the available run-time software resources cataloged
                    within the ModusToolbox™ Software repository
                    <a
                      href="https://github.com/Infineon/modustoolbox-software"
                      target="_blank"
                      >overview</a
                    >.
                  </p>
                  <p>
                    <strong>Development Tools</strong> supporting Windows,
                    Linux, and macOS are available as a
                    <a
                      href="https://softwaretools.infineon.com/tools/com.ifx.tb.tool.modustoolbox"
                      target="_blank"
                      >tools installation package</a
                    >
                    from the Infineon Developer Center. These desktop
                    applications enable the creation of new embedded
                    applications, managing software components, configuring
                    device peripherals and middleware, and embedded development
                    tools for compiling, programming, and debugging.&nbsp; The
                    ModusToolbox™ development tools interface directly with the
                    available run-time software repositories, providing easy
                    access to the latest development resources.&nbsp;
                    <a
                      href="https://softwaretools.infineon.com/tools/com.ifx.tb.tool.modustoolbox"
                      target="_blank"
                      >Download today</a
                    >
                    to get started with ModusToolbox™.
                  </p>
                  <p>
                    <strong>Community</strong> forums, knowledge-based articles,
                    and technical blog articles are easily accessible from
                    the&nbsp;<a
                      href="https://community.infineon.com/?_ga=2.196006572.14528265.1672313549-393204044.1672131587"
                      target="_blank"
                    >
                      Infineon Developer Community</a
                    >.&nbsp; Additional resources to enhance the ModusToolbox™
                    development experience include comprehensive
                    <a
                      href="https://documentation.infineon.com/html/modustoolbox-software/en/latest/Modustoolbox/Modustoolbox.html?_ga=2.194911149.14528265.1672313549-393204044.1672131587"
                      target="_blank"
                      >documentation</a
                    >
                    for both development tools and run-time software, detailed
                    <a
                      href="https://github.com/Infineon/training-modustoolbox"
                      target="_blank"
                      >training</a
                    >, and
                    <a
                      href="https://community.infineon.com/t5/Blogs/How-to-Technical-training-for-ModusToolbox/ba-p/357969?_ga=2.203814800.14528265.1672313549-393204044.1672131587"
                      target="_blank"
                      >tutorial videos</a
                    >.
                  </p>
                </v-card-text>
              </v-card>
            </v-col>
            <v-col cols="4">
              <v-img
                :src="$res('img/dashboard/mtb_logo.png')"
                max-width="214"
                max-height="214"
              ></v-img>
            </v-col>
          </v-row>
          <v-row><p ref="embeddedExamples" class="text-h5">Examples</p> </v-row>
          <v-row>
            <p>
              This section offers <strong>ready-to-use</strong> solutions that
              can be adapted through the ModusToolbox™. Please refer to the
              <a
                text
                @click="
                  navBar.solution = true
                  scrollTo('solution')
                "
                >solutions sections</a
              >
              to test, tune and access the code-examples.
            </p>
          </v-row>
          <!--
          <v-row
            ><p ref="embeddedFirmwareUpdate" class="text-h5 pt-4">
              Firmware update
            </p>
          </v-row>
          <v-row>
            <p>
              This section offers examples how to update the device firmware in
              the field. You are missing an example? Infineon offers various
              other examples related to
              <a
                href="https://github.com/Infineon?q=firmware+example&type=all&language=&sort="
                target="_blank"
                >firmware update on GitHub</a
              >.
            </p>
          </v-row>
          <v-row>
            <Card
              title="SPI - firmware update"
              description="This example demonstrates the basic DFU, with PSoC™ 6 MCU. This includes downloading an application from a Raspberry Pi and installing it in device flash, and then transferring control to that application."
              route=""
            ></Card>
          </v-row>
          <v-row
            ><p ref="embeddedDataForwarding" class="text-h5 pt-4">
              Data forwarding
            </p>
          </v-row>
          <v-row>
            <p>
              This section offers examples how to communicate or stream radar
              data to an application processor. You are missing an example?
              Infineon offers various other examples related to
              <a
                href="https://github.com/Infineon?q=uart+usb+i2c&type=all&language=&sort="
                target="_blank"
                >interfacing with a PSoC on GitHub</a
              >.
            </p>
          </v-row>
          <v-row>
            <Card
              title="SPI - Presence"
              description="This code example demonstrates implementing SPI slave device communicating the output of the Presence solution (state, distance, timestamp) to a Raspberry Pi."
            ></Card>
            <Card
              title="SPI - Raw radar data forwarding"
              description="This code example demonstrates implementing SPI slave device acquring XENSIV™ radar data. In this example, the slave device waits for start signal from master, then start acquring radar data and sending it to master."
              route="https://gitlab.intra.infineon.com/pss/sensys/csk/mtb-example-psoc6-spi-slave/-/tree/SPEAR-680_Slave_For_SOM_Board"
            ></Card>
          </v-row>
          -->
          <v-row
            ><p ref="embeddedConnectivity" class="text-h5 pt-4">Connectivity</p>
          </v-row>
          <v-row>
            <p>
              This section offers examples how to communicate with remote
              services to enable connected IoT solutions. You are missing an
              example? Infineon offers various other examples related to
              <a
                href="https://github.com/Infineon?q=connectivity+example&type=all&language=&sort="
                target="_blank"
                >connectivity on GitHub</a
              >.
            </p>
          </v-row>
          <v-row>
            <Card
              title="MQTT - Presence"
              description="This code example demonstrates implementing an MQTT client using the MQTT client library for XENSIV™ sensors with Infineon connectivity devices. The library uses the following: AWS IoT device SDK MQTT client library that includes an MQTT 3.1.1 client, RadarSensing library configured for presence detection application."
              route="https://github.com/Infineon/mtb-example-sensors-radar-anycloud-mqtt-client"
            ></Card>
            <Card
              title="UDP - Raw radar data forwarding"
              description="In this example, the UDP server waits for the UDP client to establish the connection. Once the connection is established, the server allows the user to send the commands to control the operation of radar sensor which includes start/stop or the operation of sensor in test mode or data mode."
              route="https://github.com/Infineon/mtb-example-anycloud-radar-udp-server"
            ></Card>
            <!--
            <Card
              title="Over-the-air firmware update using MQTT"
              description="This code example demonstrates Over-the-Air (OTA) firmware update using the AnyCloud SDK for PSoC 6 MCU and CYW43xxx connectivity devices."
              route="https://github.com/Infineon/mtb-example-ota-mqtt"
            ></Card>
            <Card
              title="Infineon OPTIGA™ Trust M"
              description="The OPTIGA™ Trust M is a high-end security solution that provides an anchor of trust for connecting IoT devices to the cloud, giving every IoT device its own unique identity. This pre-personalized turnkey solution offers secured, zero-touch onboarding and the high performance needed for quick cloud access."
              route="https://github.com/Infineon/optiga-trust-m#examples"
            ></Card>
            -->
          </v-row>
        </div>

        <div v-if="navBar.desktop" class="pt-12">
          <v-row>
            <p ref="desktop" class="text-h4 pt-8">Desktop development</p>
          </v-row>
          <v-row>
            <v-col>
              <v-card :max-width="maxWidthDescription" elevation="0">
                <v-card-text class="pa-0">
                  <p>
                    The
                    <a
                      href="https://artifactory.intra.infineon.com/artifactory/gen-pmm-sensys-local/ifxalgo/docs/main/index.html"
                      target="_blank"
                      ><strong>ifxalgo</strong></a
                    >
                    Python library offers various algorithms for radar
                    applications. It includes a native CMSIS based
                    implementation of most algorithm that shows how to implement
                    equivalent functionality of the Python code using "C" on
                    embedded platforms.
                  </p>
                  <p>
                    Goal of this library is to provide standardized
                    implementations for commonly used radar sensing functions
                    (e.g. processing algorithms, plotting). ifxalgo targets
                    Python-based prototyping and research applications for
                    developers who are used to work with numpy and SciPy with a
                    direct way forward to embedded platforms with an
                    implementation in "C".
                  </p>
                </v-card-text>
              </v-card>
            </v-col>
            <v-col cols="4">
              <v-img
                :src="$res('img/dashboard/python.png')"
                max-width="320"
                max-height="95"
              ></v-img>
            </v-col>
          </v-row>
          <v-row>
            <v-card
              class="ma-1"
              :max-width="maxWidthCard"
              elevation="0"
              outlined
            >
              <v-card-title>Python development</v-card-title>
              <v-card-text>
                <p>
                  Enable development with the Infineon - XENSIV™ connected
                  sensor kit (CSK). This application will forward the radar data
                  to your desktop or laptop through Wifi. Infineon offers a
                  Python SDK that enables rapid prototyping in Python with a
                  step-by-step guide to go embedded.
                </p>
              </v-card-text>
              <v-card-actions class="pt-8">
                <v-btn
                  absolute
                  bottom
                  right
                  @click="$router.push({ path: 'Desktop' })"
                >
                  Open
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-row>
        </div>
        <div v-if="navBar.system">
          <v-row>
            <p ref="system" class="text-h4 pt-8">System</p>
          </v-row>
          <v-row justify="center" align="center">
            <v-col>
              <v-card :max-width="maxWidthDescription" elevation="0">
                <v-card-title>Infineon - XENSIV™ config tool</v-card-title>
                <v-card-text class="pa-0">
                  <p>
                    Infineon's XENSIV™ connected sensor kit (CSK) is the go-to
                    IoT sensors platform for IoT device development. It enables
                    the creation of new prototype ideas based on Infineon
                    sensors including radar, environmental sensors and others.
                  </p>
                  <p>
                    The XENSIV™ connected sensor kit is a development kit for
                    customers in the IoT space looking to rapidly design,
                    develop and manufacture products based on a development
                    ecosystem. It supports testing sensor-driven IoT products
                    and use cases as well as prototyping. The kit enables
                    customers to significantly reduce the time from proof of
                    concept to a fully developed, finalized IoT design, for
                    smart home applications.
                  </p>
                  <p>
                    The development supports drivers and use cases based on
                    <a
                      href="/cms/en/product/sensor/radar-sensors/radar-sensors-for-iot/60ghz-radar/"
                      target="_blank"
                      >XENSIV™&nbsp;60&nbsp;GHz radar</a
                    >,
                    <a
                      href="/cms/en/product/sensor/co2-sensors/"
                      target="_blank"
                      >PAS CO2</a
                    >
                    and
                    <a
                      href="/cms/en/product/sensor/pressure-sensors/pressure-sensors-for-iot/dps368/"
                      target="_blank"
                      >DPS368 pressure sensor</a
                    >&nbsp;(<strong>sense</strong>) for PSoC™ 6 microcontroller
                    (<strong>compute</strong>) with Wi-Fi and Bluetooth® combo
                    (<strong>connect</strong>). The software ecosystem is
                    provided through ModusToolbox™. The sensor kit is Adafruit
                    Feather-compatible with a small form factor and can be
                    operated using a battery.
                  </p>
                  <ul>
                    <li>
                      <strong>Sense:</strong> XENSIV™ BGT60TR13C (60 GHz radar),
                      PAS CO2 and DPS368 barometric pressure sensor
                    </li>
                    <li>
                      <strong>Compute:</strong> PSoC™ 62 low-power dual-core
                      microcontroller
                    </li>
                    <li>
                      <strong>Connect: </strong>Low-power Wi-Fi (dual band – 2.4
                      and 5 GHz) and Bluetooth® 5.0 combo
                    </li>
                    <li><strong>Secure:</strong> Optiga™ Trust M</li>
                  </ul>
                </v-card-text>
              </v-card>
            </v-col>
            <v-col cols="4">
              <v-img
                :src="$res('img/dashboard/csk_hw.png')"
                max-width="320"
                max-height="273"
              ></v-img>
            </v-col>
          </v-row>
        </div>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import Card from '../components/SolutionCard.vue'

export default {
  name: 'Dashboard',

  components: { Card },

  mixins: [],

  props: {},

  data: () => ({
    navBar: {
      help: false,
      gettingstarted: true,
      solution: false,
      embedded: false,
      desktop: false,
      system: false,
    },

    maxWidthCard: 460,

    maxWidthDescription: 900,
  }),

  computed: {
    showHelp() {
      return (
        this.navBar.help ||
        (!this.navBar.gettingstarted &&
          !this.navBar.solution &&
          !this.navBar.desktop &&
          !this.navBar.embedded &&
          !this.navBar.system)
      )
    },
  },

  methods: {
    async scrollTo(element) {
      await this.$sleep(100)
      const elem = this.$refs[element]
      if (elem) {
        //console.log('scroll to ' + elem)
        elem.scrollIntoView()
      }
    },
  },

  mounted() {},
}
</script>

<style></style>
