import Vue from 'vue'
Vue.prototype.$subfolder = process.env.VUE_APP_PUBLICPATH

import App from './App.vue'
import vuetify from './utilities/Vuetify'
import router from './utilities/VueRouter'
import store from './utilities/VuexStore'
import axios from 'axios'
import wb from './registerServiceWorker'
import VueKonva from 'vue-konva'
import VCurrencyField from 'v-currency-field'
import { VTextField } from 'vuetify/lib' //Globally import VTextField

Vue.component('v-text-field', VTextField)
Vue.use(VueKonva)
Vue.use(VCurrencyField)
Vue.prototype.$workbox = wb
Vue.config.productionTip = false
Vue.prototype.$http = axios

// https://stackoverflow.com/a/4673436
if (!String.prototype.format) {
  String.prototype.format = function () {
    var args = arguments
    return this.replace(/{(\d+)}/g, function (match, number) {
      return typeof args[number] != 'undefined' ? args[number] : match
    })
  }
}

Vue.prototype.$log = {
  warn: (msg, moreDetails = '') => {
    if (msg) console.warn(msg, moreDetails)
    store.commit('onInformationMessage', {
      level: 'warn',
      color: 'orange',
      message: msg,
      details: moreDetails,
    })
  },
  info: (msg, moreDetails = '') => {
    if (msg) console.log(msg, moreDetails)
    store.commit('onInformationMessage', {
      level: 'info',
      color: undefined,
      message: msg,
      details: moreDetails,
    })
  },
  popup: (msg, moreDetails = '') => {
    if (msg) console.log(msg, moreDetails)
    store.commit('onInformationMessage', {
      level: 'popup',
      color: undefined,
      message: msg,
      details: moreDetails,
    })
  },
  error: (msg, moreDetails = '') => {
    if (msg) console.error(msg, moreDetails)
    store.commit('onInformationMessage', {
      level: 'error',
      color: 'red',
      message: msg,
      details: moreDetails,
    })
  },
}

const waitFor = (delay) => new Promise((resolve) => setTimeout(resolve, delay))
Vue.prototype.$sleep = waitFor

Vue.prototype.$openLink = (url) => {
  window.open(url, '_blank', 'noreferrer')
}

const subfolder = Vue.prototype.$subfolder
Vue.prototype.$res = (url) => {
  return subfolder ? `${subfolder}${url}` : url
}

window.$vue = new Vue({
  vuetify,
  router,
  store,
  render: (h) => h(App),
}).$mount('#app')
