<template>
  <v-app>
    <SolutionView
      key="presence"
      applicationId="PSoC™ 6 MCU : Human presence detection"
      applicationName="Presence"
      applicationShortName="Presence"
      :applicationManual="$res('manuals/CSK_Manual.pdf#page=27')"
      :applicationFirmware="$res('firmware/Presence.hex')"
      :applicationConfiguration="applicationConfiguration"
      :applicationDefaultConfiguration="applicationDefaultConfiguration"
      :onNewMessage="onNewMessage"
      :onExportCSV="onExportCSV"
      :onDeviceConnection="onDeviceConnection"
      :onExportModusToolboxConfigurationHeader="
        onExportModusToolboxConfigurationHeader
      "
    >
      <template v-slot:plotNavigationBar>
        <v-divider></v-divider>
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnPlotPresence"
              :color="plots.presence ? 'info' : undefined"
              x-large
              icon
              @click="plots.presence = !plots.presence"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-location-enter</v-icon>
            </v-btn>
          </template>
          <span>Plot Presence</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnPlotDistance"
              :color="plots.distance ? 'info' : undefined"
              x-large
              icon
              @click="plots.distance = !plots.distance"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-map-marker</v-icon>
            </v-btn>
          </template>
          <span>Plot Macro Energy over Distance</span>
        </v-tooltip>

        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnPlotMacroMotion"
              :color="plots.macro ? 'info' : undefined"
              x-large
              icon
              @click="plots.macro = !plots.macro"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-motion</v-icon>
            </v-btn>
          </template>
          <span>Plot Macro and Micro Energy</span>
        </v-tooltip>
      </template>

      <template v-slot:plotView>
        <v-row justify="center">
          <v-card
            v-if="plots.presence"
            class="ma-1"
            min-width="500"
            elevation="0"
            outlined
          >
            <Plotly
              id="cyPresencePlot"
              :key="presencePlot"
              :data="plotData.presence.traces"
              :layout="plotData.presence.layout"
              v-bind="{
                modeBarButtonsToRemove: [
                  'pan2d',
                  'select2d',
                  'lasso2d',
                  'autoScale2d',
                  //'resetScale2d',
                  'zoomOut2d',
                  'toggleSpikelines',
                  'zoom2d',
                  'zoomIn2d',
                  'zoomOut2d',
                ],
              }"
            ></Plotly>
          </v-card>

          <v-card
            v-if="plots.distance"
            class="ma-1"
            min-width="500"
            elevation="0"
            outlined
          >
            <Plotly
              id="cyDistancePlot"
              :key="distancePlot"
              :data="plotData.macroMicro.traces"
              :layout="plotData.macroMicro.layout"
              v-bind="{
                modeBarButtonsToRemove: [
                  'pan2d',
                  'select2d',
                  'lasso2d',
                  'autoScale2d',
                  //'resetScale2d',
                  'zoomOut2d',
                  'toggleSpikelines',
                  'zoom2d',
                  'zoomIn2d',
                  'zoomOut2d',
                ],
              }"
            ></Plotly>
          </v-card>

          <v-card
            v-if="plots.macro"
            class="ma-1"
            min-width="500"
            elevation="0"
            outlined
          >
            <Plotly
              id="cyMacroPlot"
              :data="plotData.macroFFT.traces"
              :layout="plotData.macroFFT.layout"
              v-bind="{
                modeBarButtonsToRemove: [
                  'pan2d',
                  'select2d',
                  'lasso2d',
                  'autoScale2d',
                  //'resetScale2d',
                  'zoomOut2d',
                  'toggleSpikelines',
                  'zoom2d',
                  'zoomIn2d',
                  'zoomOut2d',
                ],
              }"
            ></Plotly>
          </v-card>
        </v-row>
      </template>

      <template v-slot:applicationInformation>
        <p>
          This code example demonstrates Infineon's radar presence solution to
          detect human presence within a configurable distance. Powered by the
          XENSIV™ 60-GHz radar, this solution provides extremely high accuracy
          in detecting both micro and macro motions. The ability to detect micro
          motion offers unique benefits over conventional technologies deployed
          to detect human presence, thus making it perfect for user interaction
          with devices.
        </p>
        <v-btn
          class="ma-2"
          @click="
            $openLink(
              'https://www.infineon.com/cms/en/design-support/tools/sdk/modustoolbox-software/'
            )
          "
        >
          Get ModusToolbox
        </v-btn>
        <v-btn
          class="ma-2"
          @click="
            $openLink(
              'https://github.com/Infineon/mtb-example-psoc6-radar-presence'
            )
          "
        >
          <v-icon class="pr-2">mdi-github</v-icon>
          Open on GitHub
        </v-btn>

        <!--
        <v-btn
          class="ma-2"
          @click="
            $openLink(
              'https://github.com/codespaces/new?hide_repo_select=true&ref=master&repo=586806658'
            )
          "
        >
          <v-icon>mdi-github</v-icon>
          Open on codespaces
        </v-btn>
        -->
      </template>

      <template v-slot:applicationSettings>
        <v-tooltip max-width="500" right>
          <template v-slot:activator="{ on, attrs }">
            <v-select
              id="cySelectMode"
              label="Mode"
              v-model="applicationConfiguration.mode"
              :items="[
                'macro_only',
                'micro_only',
                'micro_if_macro',
                'micro_and_macro',
              ]"
              v-bind="attrs"
              v-on="on"
            ></v-select>
          </template>
          <span
            >Chose work mode to either only trigger on macro or micro motions or
            to enable a state machine that micro motions are only evaluated if
            macro motions were detected first.</span
          >
        </v-tooltip>

        <v-tooltip max-width="500" right>
          <template v-slot:activator="{ on, attrs }">
            <v-currency-field
              id="cyTextMaxRange"
              label="Max range [m]"
              v-model="applicationConfiguration.max_range"
              :max="5.0"
              :min="0.0"
              v-bind="attrs"
              v-on="on"
            ></v-currency-field>
          </template>
          <span
            >Sets the maximum range for presence algorithm in meters. Range 0.66
            .. 5.0 m</span
          >
        </v-tooltip>

        <v-tooltip max-width="500" right>
          <template v-slot:activator="{ on, attrs }">
            <v-currency-field
              id="cyTextMacroThreshold"
              label="Macro threshold"
              v-model="applicationConfiguration.macro_threshold"
              :max="100.0"
              :min="0.1"
              v-bind="attrs"
              v-on="on"
            ></v-currency-field>
          </template>
          <span
            >Sets macro threshold for presence algorithm. Range 0.1 ..
            100.0.</span
          >
        </v-tooltip>

        <v-tooltip max-width="500" right>
          <template v-slot:activator="{ on, attrs }">
            <v-currency-field
              id="cyTextMicroThreshold"
              label="Micro threshold"
              v-model="applicationConfiguration.micro_threshold"
              :max="99.0"
              :min="0.2"
              v-bind="attrs"
              v-on="on"
            ></v-currency-field>
          </template>
          <span
            >Sets micro threshold for presence algorithm. Range 0.2 ..
            99.0.</span
          >
        </v-tooltip>

        <v-tooltip max-width="500" right>
          <template v-slot:activator="{ on, attrs }">
            <div style="width: 100%" v-on="on" v-bind="attrs">
              <v-switch
                style="min-width: 200px"
                id="cySwitchVibrationFilter"
                label="Vibration filter"
                v-model="applicationConfiguration.bandpass_filter"
              ></v-switch>
            </div>
          </template>
          <span>Enable or disable vibration filter.</span>
        </v-tooltip>

        <v-tooltip max-width="500" right>
          <template v-slot:activator="{ on, attrs }">
            <div style="width: 100%" v-on="on" v-bind="attrs">
              <v-switch
                style="min-width: 200px"
                id="cySwitchDecimationFilter"
                label="Decimation filter"
                v-model="applicationConfiguration.decimation_filter"
              ></v-switch>
            </div>
          </template>
          <span>Enable or disable decimation filter.</span>
        </v-tooltip>
      </template>
    </SolutionView>
  </v-app>
</template>

<script>
import SolutionView from '../components/SolutionView'

import { Plotly } from 'vue-plotly'

const RangeBin2Meter = 0.325861365

const defaultEventObject = {
  timestamp: 0, // timestamp in ms since start of device
  state: '', // absent, micro, macro
  distance: 0, // m
}

const defaultConfiguration = {
  mode: 'micro_if_macro',
  max_range: 2.0,
  macro_threshold: 1.0,
  micro_threshold: 25.0,
  bandpass_filter: false,
  decimation_filter: false,
}

const defaultData = {
  presence: {
    traces: [
      {
        x: [],
        y: [],
        line: { shape: 'hv', color: '#9BC3B7' },
        mode: 'lines',
        name: 'Presence state',
        type: 'scatter',
      },
    ],
    layout: {
      title: 'Presence state',
      yaxis: {
        title: 'Presence state',
        autorange: false,
        type: 'category',
        categoryorder: 'array',
        categoryarray: ['absence', 'macro', 'micro'],
      },
      xaxis: {
        title: 'Timestamp [s]',
        autorange: false,
        range: [0.0, 1.0],
        type: 'linear',
      },
    },
  },

  macroMicro: {
    traces: [
      {
        name: 'Macro motion',
        marker: { color: '#9BC3B7' },
        type: 'bar',
        x: [], // timestamp
        y: [], // energy
        text: [], // range bin -> m
      },
      {
        name: 'Micro motion',
        marker: { color: '#AEC067' },
        type: 'bar',
        x: [], // timestamp
        y: [],
        text: [],
      },
    ],
    layout: {
      title: 'Macro and micro energy @ distance',
      yaxis: {
        title: 'Energy',
        autorange: false,
        range: [0.0, 1.0],
        type: 'linear',
      },
      showlegend: true,
      legend: {
        x: 1,
        xanchor: 'right',
        y: 1,
      },
      xaxis: {
        title: 'Timestamp [s]',
        autorange: false,
        range: [0.0, 1.0],
        type: 'linear',
      },
    },
  },

  macroFFT: {
    traces: [
      {
        x: [],
        y: [],
        line: { shape: 'hv', color: '#9BC3B7' },
        mode: 'lines',
        name: 'Macro motion',
        type: 'scatter',
      },
      {
        x: [],
        y: [],
        line: { shape: 'hv', color: '#AEC067' },
        mode: 'lines',
        name: 'Threshold',
        type: 'scatter',
      },
    ],
    layout: {
      title: 'Macro energy',
      yaxis: {
        title: 'Macro energy',
        autorange: false,
        range: [0.0, 1.0],
        type: 'linear',
      },
      showlegend: true,
      legend: {
        x: 1,
        xanchor: 'right',
        y: 1,
      },
      xaxis: {
        title: 'Distance [m]',
        //autorange: false,
        //range: [0.0, 1.0],
        type: 'linear',
      },
    },
  },
}

export default {
  name: 'Presence',

  components: { SolutionView, Plotly },

  props: {},

  data: () => ({
    presencePlot: 'presence',
    distancePlot: 'distance',

    plots: {
      presence: true,
      distance: false,
      macro: false,
    },

    applicationDefaultConfiguration: JSON.parse(
      JSON.stringify(defaultConfiguration)
    ),

    applicationConfiguration: JSON.parse(JSON.stringify(defaultConfiguration)),

    plotData: JSON.parse(JSON.stringify(defaultData)),
  }),

  computed: {},

  methods: {
    processMessageMACRO_FFT(line) {
      let x = []
      let y = []
      let threshold = []

      const lineSplit = line.split(' ')
      // const timestamp = parseFloat(lineSplit[1]) / 1000
      for (let i = 0; i < lineSplit.length - 2; i++) {
        const strValue = lineSplit[i + 2]
        if (strValue.trim().length !== 0) {
          const yValue = parseFloat(lineSplit[i + 2])
          y.push(yValue)
          x.push(i * RangeBin2Meter)

          threshold.push(this.applicationConfiguration.macro_threshold)
        }
      }

      // round to next 10's
      let yMaxRange = this.applicationConfiguration.macro_threshold
      yMaxRange = Math.max(yMaxRange, ...y)
      yMaxRange = Math.round(yMaxRange)
      yMaxRange = Math.round(yMaxRange / 10 + 1) * 10

      this.plotData.macroFFT.traces[0].x = x
      this.plotData.macroFFT.traces[0].y = y
      this.plotData.macroFFT.traces[1].x = x
      this.plotData.macroFFT.traces[1].y = threshold

      this.plotData.macroFFT.layout.yaxis.range = [0.0, yMaxRange]

      return null
    },

    processMessageMACRO_MICRO(line) {
      const lineSplit = line.split(' ')
      if (!(lineSplit.length >= 4)) {
        console.warn('Invalid number of argumets, got = ' + line)
        return
      }

      const macroData = this.plotData.macroMicro.traces[0]
      const microData = this.plotData.macroMicro.traces[1]

      let yValue = lineSplit[2] // Energy
      let xValue = parseFloat(lineSplit[3]) / 1000 // Timestamp
      let dValue = parseFloat(lineSplit[1]) * RangeBin2Meter // Range Bin

      if (xValue) {
        if (line.startsWith('[MACRO]')) {
          macroData.y.push(yValue)
          macroData.x.push(xValue)
          macroData.text.push(`@ ${dValue}m`)
        } else if (line.startsWith('[MICRO]')) {
          microData.y.push(yValue)
          microData.x.push(xValue)
          microData.text.push(`@ ${dValue}m`)
        }

        // check if duration has been passed
        if (xValue - 30.5 > macroData.x[0]) {
          macroData.y.shift()
          macroData.x.shift()
          macroData.text.shift()
        }

        if (xValue - 30.5 > microData.x[0]) {
          microData.y.shift()
          microData.x.shift()
          microData.text.shift()
        }
      }

      // round to next 10's
      let yMaxRange = this.applicationConfiguration.macro_threshold
      yMaxRange = Math.round(
        Math.max(yMaxRange, ...macroData.y, ...microData.y)
      )
      yMaxRange = Math.round(yMaxRange / 10 + 1) * 10

      this.plotData.macroMicro.layout.yaxis.range = [0.0, yMaxRange]
      this.plotData.macroMicro.layout.xaxis.range = [
        macroData.x[0],
        macroData.x.at(-1),
      ]

      // force redraw of component, it seems that without this line the data is not picked up
      this.distancePlot = 'distance' + microData.x.at(-1)

      return null
    },

    processMessageINFO(line) {
      const lineSplit = line.split(' ')

      let yValue = null
      let xValue = null
      let dValue = 0
      // "[INFO] macro presence %" PRIi32 " %" PRIi32 "\n"
      if (lineSplit.length >= 5) {
        yValue = lineSplit[1]
        xValue = parseFloat(lineSplit[4]) / 1000
        dValue = parseFloat(lineSplit[3]) * RangeBin2Meter
      }
      // "[INFO] absence %" PRIu32 "\n"
      else if (lineSplit.length >= 3) {
        yValue = lineSplit[1]
        xValue = parseFloat(lineSplit[2]) / 1000
        dValue = 0.0
      } else {
        console.warn('Invalid number of argumets, got = ' + line)
        return null
      }

      const trace = this.plotData.presence.traces[0]
      trace.x.push(xValue)
      trace.y.push(yValue)

      if (xValue - 30.5 > trace.x[0]) {
        trace.x.shift()
        trace.y.shift()
      }
      //this.plotData.presence.traces = [{ ...trace }]

      const layout = this.plotData.presence.layout
      layout.xaxis.range = [trace.x[0], trace.x.at(-1)]

      // force redraw of component, it seems that without this line the data is not picked up
      this.presencePlot = 'presence' + trace.x.at(-1)
      // const traces = [{ ...trace }]
      // this.plotData.presence.traces = traces
      // this.$refs.cyPresencePlot.react()
      //this.$refs.cyPresencePlot.schedule({ replot: true })

      const eventObject = {
        ...defaultEventObject,
        ...{
          timestamp: xValue, // timestamp in ms since start of device
          state: yValue, // absent, micro, macro
          distance: dValue ? dValue : '', // m
        },
      }

      return eventObject
    },

    onNewMessage(msg) {
      try {
        if (msg.startsWith('[INFO]')) {
          return this.processMessageINFO(msg)
        } else if (msg.startsWith('[MACRO_FFT]')) {
          return this.processMessageMACRO_FFT(msg)
        } else if (msg.startsWith('[MACRO]') || msg.startsWith('[MICRO]')) {
          return this.processMessageMACRO_MICRO(msg)
        }
      } catch (error) {
        this.$log.warn(
          'Failed to parse firmware response, msg = "' +
            msg +
            '", error = ' +
            error
        )
      }

      return false
    },

    onExportCSV(terminalEvents) {
      const csvFile = []
      csvFile.push(
        'Timestamp since device startup [s];Presence state;Distance [m]'
      )

      for (const obj of terminalEvents) {
        const timestamp = obj.timestamp
        const state = obj.state
        const distance = obj.distance

        const csvLine = `${timestamp};${state};${distance}`
        csvFile.push(csvLine)
      }

      return csvFile
    },

    onExportModusToolboxConfigurationHeader() {
      const micro_fft_decimation_enabled = this.applicationConfiguration
        .decimation_filter
        ? 'true'
        : 'false'
      const macro_fft_bandpass_filter_enabled = this.applicationConfiguration
        .bandpass_filter
        ? 'true'
        : 'false'
      const macro_threshold = this.applicationConfiguration.macro_threshold
      const micro_threshold = this.applicationConfiguration.micro_threshold
      const max_range_bin = Math.ceil(
        this.applicationConfiguration.max_range / RangeBin2Meter
      )
      const mode_mapping = {
        macro_only: 'XENSIV_RADAR_PRESENCE_MODE_MACRO_ONLY',
        micro_only: 'XENSIV_RADAR_PRESENCE_MODE_MICRO_ONLY',
        micro_if_macro: 'XENSIV_RADAR_PRESENCE_MODE_MICRO_IF_MACRO',
        micro_and_macro: 'XENSIV_RADAR_PRESENCE_MODE_MICRO_AND_MACRO',
      }
      const mode = mode_mapping[this.applicationConfiguration.mode]

      const header = `
#include "xensiv_radar_presence.h"

// Please note this is an auto-generated configuration based on the default radar
// settings shipped with the Presence example. 
// Any customization of the radar settings is not reflected in this configuration.  
static const xensiv_radar_presence_config_t default_config =
{
  .bandwidth                         = 460E6,
  .num_samples_per_chirp             = XENSIV_BGT60TRXX_CONF_NUM_SAMPLES_PER_CHIRP,
  .micro_fft_decimation_enabled      = ${micro_fft_decimation_enabled},
  .micro_fft_size                    = 128,
  .macro_threshold                   = ${macro_threshold},
  .micro_threshold                   = ${micro_threshold},
  .min_range_bin                     = 1,
  .max_range_bin                     = ${max_range_bin},
  .macro_compare_interval_ms         = 250,
  .macro_movement_validity_ms        = 1000,
  .micro_movement_validity_ms        = 4000,
  .macro_movement_confirmations      = 0,
  .macro_trigger_range               = 1,
  .mode                              = ${mode},
  .macro_fft_bandpass_filter_enabled = ${macro_fft_bandpass_filter_enabled},
  .micro_movement_compare_idx       = 5
};
`
      return header
    },

    onDeviceConnection(connected, deviceConfiguration) {
      this.applicationConfiguration = connected
        ? JSON.parse(JSON.stringify(deviceConfiguration))
        : JSON.parse(JSON.stringify(defaultConfiguration))

      this.plotData = JSON.parse(JSON.stringify(defaultData))
    },
  },
}
</script>

<style></style>
