<template>
  <v-app>
    <router-view />

    <v-snackbar v-model="infoMessageVisible" multi-line top :timeout="3000">
      {{ infoMessageReceived ? infoMessageReceived.message : '' }}
    </v-snackbar>

    <v-dialog
      class="mx-auto"
      max-width="600"
      persistent
      v-model="noWebSerialSupport"
    >
      <v-card>
        <v-card-title class="headline">
          WebSerial is not supported on your webbrowser
        </v-card-title>
        <v-card-text>
          WebSerial is required to connect to the
          <a
            :href="'https://www.infineon.com/cms/de/applications/solutions/sensor-solutions/iot-sensors-platform/'"
            target="_blank"
            >Infineon - XENSIV™ config tool</a
          >. It seems that your webbrower does not support this feature. Please
          open this webpage in
          <a :href="'https://www.google.com/chrome/'" target="_blank">Chrome</a>
          or
          <a :href="'https://www.microsoft.com/edge'" target="_blank">Edge</a>.
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
export default {
  name: 'App',

  components: {},

  mixins: [],

  data: () => ({
    infoMessageVisible: false,

    noWebSerialSupport: true,
  }),

  computed: {
    infoMessageReceived() {
      const msg = this.$store.getters.infoMessageReceived
      return msg
    },
  },

  watch: {
    infoMessageReceived(newValue) {
      if (newValue && newValue.level !== 'info') {
        this.infoMessageVisible = true
      }
    },
  },

  mounted() {
    // remove loading animation
    const spinner = document.querySelector('app')
    if (spinner) {
      spinner.classList.remove('spin')
      document.getElementById('#logo').remove()
    }

    // check for WebSerial API support
    this.noWebSerialSupport = 'serial' in navigator ? false : true
  },

  created() {
    if (this.$workbox) {
      this.$workbox.addEventListener('waiting', () => {
        this.showUpgradeUI = true
      })
    }
  },

  methods: {
    async accept() {
      this.showUpgradeUI = false
      await this.$workbox.messageSW({ type: 'SKIP_WAITING' })
    },
  },
}
</script>

<style>
/* https://ishwar-rimal.medium.com/fixing-that-address-bar-issue-in-mobile-browsers-once-and-for-all-8c283fc88e56 */
html {
  overflow-y: hidden;
}

.v-progress-circular__underlay {
  stroke: #d3d3d3 !important;
}

#cyTerminal {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 3px grey;
  background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.terminal-row {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
