<script>
// requires Terminal and Download mixin
// and this.applicationDefaultConfiguration
export default {
  methods: {
    async configSet(command, value, key = undefined, popup = false) {
      let config_value = value

      // boolean type will be converted to 'enable' or 'disable'
      if (value === false || value === true) {
        value = value ? 'enable' : 'disable'
      }

      if (popup) {
        const terminalCommand = `${command} ${value}`
        const timeout = 150
        const attributes = await this.terminalQueryAttributes(
          terminalCommand,
          '[CONFIG]',
          timeout
        )

        const attributeKey = !key ? command : key
        if ('Failed' in attributes) {
          console.warn(
            'Configuration attribute could not be applied.',
            attributeKey,
            config_value
          )
          return
        }

        const finalValue = this.applicationConfiguration[attributeKey]
        const msg = `Configuration changed ${attributeKey} to ${finalValue}.`
        this.$log.popup(msg)
      } else {
        const terminalCommand = `\x0D${command} ${value}\x0D\x1B`
        await this.terminalWrite(terminalCommand)
      }
    },

    /**
     * Call configSet for each of the attributes
     */
    async configSetDictionary(configuration, prefix = 'set_') {
      try {
        const config = JSON.parse(JSON.stringify(configuration))

        for (const [key, value] of Object.entries(config)) {
          const command = `${prefix}${key}`
          await this.configSet(command, value, key)
        }

        return await this.terminalQueryAttributes('solution_config', '[CONFIG]')
      } catch (error) {
        this.$log.warn('Could not apply configuration, error = ' + error)

        return null
      }
    },

    async configSave(filename) {
      let deviceInfo = this.terminalDeviceInfo
      if (!deviceInfo) {
        this.$log.warn(
          'Device information was not found. Please reconnect to the device and try again.'
        )
        return
      }

      if (!filename) {
        filename = deviceInfo.application + `_Settings.json`
      }

      // deep copy to create new config object
      const config = JSON.parse(JSON.stringify(deviceInfo))
      config.config = JSON.parse(JSON.stringify(this.applicationConfiguration))

      const json = JSON.stringify(config, null, 2)
      const blob = new Blob([json], { type: 'application/json' })
      return await this.downloadBlob(blob, filename)
    },

    async configLoad(file) {
      const isFile = (file) => 'File' in window && file instanceof File
      if (!isFile) {
        this.$log.warn(
          'Invalid input, expected type File got ' + typeof file,
          file
        )
        return null
      }

      let deviceInfo = this.terminalDeviceInfo
      if (!deviceInfo) {
        this.$log.warn(
          'Device information was not found. Please reconnect to the device and try again.'
        )
        return null
      }

      async function configGetFileContent(file) {
        return new Promise((resolve, reject) => {
          let contents = ''
          const reader = new FileReader()
          reader.onloadend = function (e) {
            contents = e.target.result
            resolve(contents)
          }
          reader.onerror = function (e) {
            reject(e)
          }
          reader.readAsText(file)
        })
      }

      const file_content = await configGetFileContent(file)
      try {
        const loadedConfig = JSON.parse(file_content)
        const application = deviceInfo.application
        if (
          !(
            'application' in loadedConfig &&
            loadedConfig['application'] === deviceInfo.application
          )
        ) {
          this.$log.warn(
            `This configuration-file is not meant for the running application "${application}"`
          )
          return null
        }

        return loadedConfig
      } catch (error) {
        this.$log.warn('Could not load configuration file, error = ' + error)
      }

      return null
    },
  },

  computed: {},
}
</script>
