<template>
  <v-app>
    <SolutionView
      key="motionangle"
      applicationId="PSoC™ 6 MCU : Motion angle"
      applicationName="Motion Angle"
      applicationShortName="MotionAngle"
      :applicationManual="$res('manuals/CSK_Manual.pdf')"
      :applicationFirmware="$res('firmware/MotionAngle.hex')"
      :applicationConfiguration="applicationConfiguration"
      :applicationDefaultConfiguration="applicationDefaultConfiguration"
      :onNewMessage="onNewMessage"
      :onExportCSV="onExportCSV"
      :onDeviceConnection="onDeviceConnection"
    >
      <template v-slot:plotNavigationBar>
        <v-divider></v-divider>
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              id="cyBtnPlotDistance"
              :color="plots.showRadarView ? 'info' : undefined"
              x-large
              icon
              @click="plots.showRadarView = !plots.showRadarView"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-map-marker</v-icon>
            </v-btn>
          </template>
          <span>Plot radar view of closest moving object</span>
        </v-tooltip>
      </template>

      <template v-slot:plotView>
        <v-row v-if="plots.showRadarView" justify="center">
          <RadarView v-bind:objectA="lastObjectEvent" />
        </v-row>
      </template>

      <template v-slot:applicationInformation>
        <p>
          This solution provides the closest moving object with a very low
          latency including distance, speed and angle information. It was
          designed and optimized to run with very little memory consumption.
        </p>

        <v-btn
          class="ma-2"
          @click="
            $openLink(
              'https://www.infineon.com/cms/en/design-support/tools/sdk/modustoolbox-software/'
            )
          "
        >
          Download ModusToolbox
        </v-btn>
        <v-btn
          disabled
          class="ma-2"
          @click="
            $openLink(
              'https://github.com/Infineon/mtb-example-sensors-radar-motionangle-freertos'
            )
          "
        >
          <v-icon>mdi-github</v-icon>
          Open on GitHub
        </v-btn>
      </template>

      <template v-slot:applicationSettings>
        <v-tooltip max-width="500" right>
          <template v-slot:activator="{ on, attrs }">
            <v-currency-field
              id="cyTextMaxRange"
              label="Max range [m]"
              :min="0.0"
              v-model="applicationConfiguration.max_range"
              v-bind="attrs"
              v-on="on"
            ></v-currency-field>
          </template>
          <span>Sets the maximum range in meters.</span>
        </v-tooltip>

        <!--
        <v-tooltip max-width="500" right>
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              id="cyTextSensitivityThreshold"
              label="Sensitivity threshold"
              type="number"
              min="0.0"
              max="1.0"
              v-model.number="applicationConfiguration.sensitivity_threshold"
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <span>Sets sensitivity threshold. [0.0 .. 1.0].</span>
        </v-tooltip>
        -->
      </template>
    </SolutionView>
  </v-app>
</template>

<script>
import SolutionView from '../components/SolutionView'
import RadarView from '../components/RadarView'

const defaultEventObject = {
  visible: false, // object is visible
  timestamp: 0, // timestamp in ms since start of device
  id: '', // object id
  distance: 0, // m
  azimuth: 0, // degree
  elevation: 0, // degree
  speed: 0, // m/s
}

const defaultConfiguration = {
  max_range: 16.0,
  // sensitivity_threshold: 0.5,
}

export default {
  name: 'MotionAngle',

  components: { SolutionView, RadarView },

  data: () => ({
    plots: {
      showRadarView: true,
    },

    applicationDefaultConfiguration: { ...defaultConfiguration },

    applicationConfiguration: { ...defaultConfiguration },

    lastObjectEvent: { ...defaultEventObject },
  }),

  methods: {
    onNewMessage(msg) {
      try {
        if (msg.startsWith('[INFO]')) {
          const lineSplit = msg.split(' ')

          const object = { ...defaultEventObject }

          // [INFO] [IN, OUT] [TIMESTAMP] [[OBJ] [DISTANCE] [AZIMUTH] [ELEVATION] [SPEED]]
          if (lineSplit.length >= 3) {
            object.event = lineSplit[1]
            object.visible = lineSplit[1] === 'IN' ? true : false
            object.timestamp = parseFloat(lineSplit[2]) / 1000
          }

          if (lineSplit.length >= 7) {
            object.id = lineSplit[3]
            object.distance = parseFloat(lineSplit[4])
            object.azimuth = parseFloat(lineSplit[5])
            object.elevation = parseFloat(lineSplit[6])
            object.speed = parseFloat(lineSplit[7])
          }

          this.lastObjectEvent = object

          console.log(msg)

          return object
        }
      } catch (error) {
        this.$log.warn('Failed to parse firmware response, msg = ' + msg)
      }

      return false
    },

    onExportCSV(terminalEvents) {
      const csvFile = []
      csvFile.push(
        'Timestamp since device startup [s];Object;Event [IN, OUT];Distance [m];Azimuth [deg];Elevation [deg];Speed [m/s]'
      )

      for (const obj of terminalEvents) {
        const timestamp = obj.timestamp
        const id = obj.id
        const action = obj.event
        const distance = obj.distance
        const azimuth = obj.azimuth
        const elevation = obj.elevation
        const speed = obj.speed

        const csvLine = `${timestamp};${id};${action};${distance};${azimuth};${elevation};${speed}`
        csvFile.push(csvLine)
      }

      return csvFile
    },

    onDeviceConnection(connected, deviceConfiguration) {
      this.applicationConfiguration = connected
        ? deviceConfiguration
        : { ...defaultConfiguration }
    },
  },
}
</script>
