<template>
  <v-app>
    <SolutionView
      key="desktop"
      applicationId="PSoC™ 6 MCU : Desktop Development"
      applicationName="Desktop Development"
      applicationShortName="Desktop Development"
      :applicationManual="$res('manuals/CSK_Manual.pdf')"
      :applicationConfiguration="applicationConfiguration"
      :applicationDefaultConfiguration="applicationDefaultConfiguration"
      :onNewMessage="onNewMessage"
      :onDeviceConnection="onDeviceConnection"
    >
      <template v-slot:applicationInformation>
        <p>
          Enable development with the Infineon - XENSIV™ connected sensor kit
          (CSK). This application will forward the radar data to your desktop or
          laptop through Wifi. Infineon offers a Python SDK that enables rapid
          prototyping in Python with a step-by-step guide to go embedded.
        </p>
      </template>

      <template v-slot:plotView>
        <v-row justify="center" style="height: calc(100% - 56px); width: 100%">
          <iframe
            style="width: 100%"
            src="https://artifactory.intra.infineon.com/artifactory/gen-pmm-sensys-local/ifxalgo/docs/main/readme.html"
            frameborder="0"
          ></iframe>
        </v-row>
      </template>

      <template v-slot:applicationSettings>
        <v-tooltip max-width="500" right>
          <template v-slot:activator="{ on, attrs }">
            <v-switch
              id="cyHotspot"
              :label="
                applicationConfiguration.hotspot
                  ? 'Wifi hotspot'
                  : 'Wifi client'
              "
              v-model="applicationConfiguration.hotspot"
              v-bind="attrs"
              v-on="on"
            ></v-switch>
          </template>
          <span
            >Enable either a Wifi-hotspot on the device or connect to an
            exsiting Wifi network.</span
          >
        </v-tooltip>

        <div v-if="applicationConfiguration.hotspot">
          <v-tooltip max-width="500" right>
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                id="cyAPSSID"
                label="Network name"
                :min="0.0"
                v-model="applicationConfiguration.ap.ssid"
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <span>Network name</span>
          </v-tooltip>

          <v-tooltip max-width="500" right>
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                id="cyAPPassword"
                label="Network password"
                v-model="applicationConfiguration.ap.password"
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <span>Network password</span>
          </v-tooltip>

          <v-tooltip max-width="500" right>
            <template v-slot:activator="{ on, attrs }">
              <v-select
                id="cyAPSecurity"
                label="Network bandwidth"
                v-model="applicationConfiguration.ap.bandwidth"
                :items="networkBandwidth"
                item-text="name"
                item-value="value"
                v-bind="attrs"
                v-on="on"
              ></v-select>
            </template>
            <span>Network bandwidth</span>
          </v-tooltip>

          <v-tooltip max-width="500" right>
            <template v-slot:activator="{ on, attrs }">
              <v-select
                id="cyAPSecurity"
                label="Network security"
                v-model="applicationConfiguration.ap.security"
                :items="securityAP"
                item-text="name"
                item-value="value"
                v-bind="attrs"
                v-on="on"
              ></v-select>
            </template>
            <span>Network security</span>
          </v-tooltip>
        </div>
        <div v-else>
          <v-tooltip max-width="500" right>
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                id="cySTASSID"
                label="Network name"
                :min="0.0"
                v-model="applicationConfiguration.sta.ssid"
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <span>Network name</span>
          </v-tooltip>

          <v-tooltip max-width="500" right>
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                id="cySTAPassword"
                label="Network password"
                v-model="applicationConfiguration.sta.password"
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <span>Network password</span>
          </v-tooltip>
        </div>
      </template>
    </SolutionView>
  </v-app>
</template>

<script>
import SolutionView from '../components/SolutionView'

const securityAP = [
  {
    value: 3,
    name: 'WPA2-Personal',
  },
  {
    value: 2,
    name: 'WPA2/WPA3-Personal',
  },
  {
    value: 1,
    name: 'WPA3-Personal',
  },
  {
    value: 0,
    name: 'Open',
  },
]

const networkBandwidth = [
  {
    value: 0,
    name: '2.4 Ghz',
  },
  {
    value: 1,
    name: '5 GHz',
  },
]

const defaultConfiguration = {
  hotspot: true,

  ap: {
    ssid: 'Infineon_CSK',
    password: 'Infineon',
    security: 3,
    bandwidth: 1,
  },
  sta: {
    ssid: '',
    password: '',
  },
}

export default {
  name: 'DesktopDevelopment',

  components: { SolutionView },

  data: () => ({
    securityAP: securityAP,

    networkBandwidth: networkBandwidth,

    applicationDefaultConfiguration: { ...defaultConfiguration },

    applicationConfiguration: { ...defaultConfiguration },
  }),

  methods: {
    onNewMessage(msg) {
      return false
    },

    onDeviceConnection(connected, deviceConfiguration) {
      this.applicationConfiguration = connected
        ? deviceConfiguration
        : { ...defaultConfiguration }
    },
  },
}
</script>
