<template>
  <v-stage :config="configKonva">
    <v-layer>
      <v-arc :config="configStage.configArc4_5"></v-arc>
      <v-arc :config="configStage.configArc3_4"></v-arc>
      <v-arc :config="configStage.configArc2_3"></v-arc>
      <v-arc :config="configStage.configArc1_2"></v-arc>
      <v-arc :config="configStage.configArc0_1"></v-arc>

      <v-circle :config="configObjectA"></v-circle>
      <v-circle :config="configObjectB"></v-circle>
    </v-layer>
  </v-stage>
</template>

<script>
const canvasWidth = 800
const canvasHeight = 600
const canvasOrigin = 530

const angle = 90
const fill = 'white'
const stroke = 'black'
const strokeWidth = 2
const rotation = 225

const objectRadius = 20

const defaultObject = {
  visible: false,
  distance: 0, // m
  azimuth: 0, // degree
  elevation: 0, // degree
  speed: 0, // m/s
}

export default {
  name: 'RadarView',

  props: {
    objectA: {
      type: Object,
      default(prop) {
        return defaultObject
      },
    },

    objectB: {
      type: Object,
      default(prop) {
        return defaultObject
      },
    },
  },

  data: () => ({
    configKonva: {
      width: canvasWidth,
      height: canvasHeight,
    },
  }),

  methods: {
    buildConfigObject(propObj) {
      const distance = propObj.distance // m
      const azimuth = propObj.azimuth // degree

      const dx = Math.sin((azimuth / 180) * Math.PI) * distance * 100
      const dz = Math.cos((azimuth / 180) * Math.PI) * distance * 100

      const obj = {
        visible: propObj.visible,
        x: canvasWidth / 2 + dx,
        y: canvasOrigin - dz,
        radius: objectRadius,
        fill: '#B1CFC6',
        stroke: stroke,
        strokeWidth: strokeWidth,
      }

      return obj
    },
  },

  computed: {
    configObjectA() {
      return this.buildConfigObject(this.objectA)
    },

    configObjectB() {
      return this.buildConfigObject(this.objectB)
    },

    configStage() {
      const stage = {
        configArc0_1: {
          x: canvasWidth / 2,
          y: canvasOrigin,
          innerRadius: 0,
          outerRadius: 100,
          angle: angle,
          fill: fill,
          stroke: stroke,
          strokeWidth: strokeWidth,
          rotation: rotation,
        },

        configArc1_2: {
          x: canvasWidth / 2,
          y: canvasOrigin,
          innerRadius: 100,
          outerRadius: 200,
          angle: angle,
          fill: fill,
          stroke: stroke,
          strokeWidth: strokeWidth,
          rotation: rotation,
        },

        configArc2_3: {
          x: canvasWidth / 2,
          y: canvasOrigin,
          innerRadius: 200,
          outerRadius: 300,
          angle: angle,
          fill: fill,
          stroke: stroke,
          strokeWidth: strokeWidth,
          rotation: rotation,
        },

        configArc3_4: {
          x: canvasWidth / 2,
          y: canvasOrigin,
          innerRadius: 300,
          outerRadius: canvasWidth / 2,
          angle: angle,
          fill: fill,
          stroke: stroke,
          strokeWidth: strokeWidth,
          rotation: rotation,
        },

        configArc4_5: {
          x: canvasWidth / 2,
          y: canvasOrigin,
          innerRadius: canvasWidth / 2,
          outerRadius: 500,
          angle: angle,
          fill: fill,
          stroke: stroke,
          strokeWidth: strokeWidth,
          rotation: rotation,
        },
      }

      return stage
    },
  },
}
</script>
